import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList, AngularFireDatabaseModule } from 'angularfire2/database'
import * as firebase from 'firebase'
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router'
@Injectable({
  providedIn: 'root'
})
export class ServiceService {
  tempateList: AngularFireList<any>
  data: any;
  file: any
  constructor(private fireBase: AngularFireDatabase,
    private router: Router,
    public toastr: ToastrService, public spinner: NgxSpinnerService, ) { }

  getData() {
    let data = this.fireBase.list('template');
    // console.log(this.data);\
    return data;
  }
  getTemplate() {
    let data = this.fireBase.list('template/0')
    return data
  }
  addTemplate(obj, url) {
    let newKey = new Date().getTime();
    this.fireBase.object(`/template/${newKey}`).set({
      name: obj.name,
      elements: obj.elements,
      file: url
    });
  }
  updateData(file, obj) {
    console.log(typeof file)
    if (typeof file == 'string') {
      this.fireBase.object('/template/' + obj.$key).update(
        {
          name: obj.name,
          elements: obj.elements,
          file: file
        }
      )
      this.toastr.success("Update Successfully")

    }
    if (typeof file == 'object') {
      let storageRef = firebase.storage().ref()
      let uploadTask = storageRef.child("upload/" + `${file.name}`).put(file);
      console.log(storageRef)
      uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
        (data) => {
          //upload in progress
          this.spinner.show()
        },
        (err) => {
        },
        () => {
          let download = firebase.storage().ref('upload/' + `${file.name}`)
          download.getDownloadURL().then(url => {
            // this.addTemplate(obj, url)
            this.fireBase.object('/template/' + obj.$key).update(
              {
                name: obj.name,
                elements: obj.elements,
                file: url
              }
            )
            this.toastr.success("Update Successfully")
            this.spinner.hide()
          })

        }
      )
    }

  }
  deleteTemplate($key: string) {
    this.fireBase.list("template").remove($key)
  }
  uploadFile(file, obj) {
    console.log(file)
    let storageRef = firebase.storage().ref()
    let uploadTask = storageRef.child("upload/" + `${file.name}`).put(file);

    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
      (data) => {
        //upload in progress
        console.log((data['bytesTransferred'] / data['totalBytes']) * 100)
        this.spinner.show()
      },
      (err) => {
        console.log(err)
      },
      () => {
        let download = firebase.storage().ref('upload/' + `${file.name}`)
        download.getDownloadURL().then(url => {
          console.log(url)
          this.addTemplate(obj, url)
          this.toastr.success("Add Template Sucessfully")
          this.spinner.hide()
        })

        // this.file.url = uploadTask.snapshot.downloadURL;
        // file.name = file.name
        // this.saveFileData(file)
        // this.addTemplate(obj)
      }
    )


  }
}
