import { Component, OnInit } from '@angular/core';
import 'fabric'
import { ServiceService } from '../service/service.service';
declare const fabric: any;
declare const $: any
import { ToastrService } from 'ngx-toastr'
import { FormGroup, FormBuilder, Validators } from '@angular/forms'
import { Router } from '@angular/router';
@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.css']
})
export class AddComponent implements OnInit {
  submitted = false
  canvas: any;
  isDown: boolean;
  rect: any
  rect2: any
  origX: any;
  origY: any
  file: File = null;
  array = new Array
  flagForNew: boolean = false
  flagForMove: boolean = false
  listTemplate = new Array
  form: FormGroup
  canvasActive: boolean = true;
  keepState: boolean;
  flagNumber : any;
  arrayShoworHide = new Array
  arrayOfCanvas = new Array
  constructor(
    private getData: ServiceService,
    private toastr: ToastrService,
    private formBuilder: FormBuilder,
    private router: Router
  ) { }
  ngOnInit() {
    this.canvas = new fabric.Canvas('canvas', {});
    this.form = this.formBuilder.group({
      name: ['', Validators.required]
    })
    this.loadDataFromService()

  }
  get f() {
    return this.form.controls;
  }
  loadDataFromService() {
    this.listTemplate = []
    // this.getData.getTemplate().snapshotChanges().subscribe(i => {
    //   console.log(i[0].payload.val()[0]);
    // });
    let data = this.getData.getData();
    data.snapshotChanges().subscribe(item => {
      item.forEach(element => {
        this.listTemplate.push(element.payload.val())
      });
    })
  }
  handleDrop(e) {
    this.file = e.dataTransfer.files[0];
    console.log(this.file);
    const reader = new FileReader();
    reader.onload = (imgFile) => {
      console.log(imgFile)
      const data = imgFile.target["result"];
      fabric.Image.fromURL(data, (img) => {
        let oImg = img.set({
          left: 0,
          top: 0,
          angle: 0,
          evented: false
        });
        img.scaleToHeight(450);
        img.scaleToWidth(600);
        this.canvas.add(oImg).renderAll();
        // var a = this.canvas.setActiveObject(oImg);
        var dataURL = this.canvas.toDataURL({ format: 'png', quality: 0.8 });
      });
    };
    reader.readAsDataURL(this.file);
    return false;
  }
  show(e) {
    this.file = e.target.files[0];
    // this.getData.uploadFile(this.file)
    const reader = new FileReader();
    reader.onload = (imgFile) => {
      const data = imgFile.target["result"];
      fabric.Image.fromURL(data, (img) => {
        let oImg = img.set({
          left: 0,
          top: 0,
          angle: 0,
          evented: false
        });
        img.scaleToHeight(450);
        img.scaleToWidth(600);
        this.canvas.add(oImg);
        // var a = this.canvas.setActiveObject(oImg);
        // var dataURL = this.canvas.toDataURL({ format: 'png', quality: 0.8 });
      });
    };
    reader.readAsDataURL(this.file);
    return false;
  }
  draw(o) {
    if (this.canvas.getObjects().length == 0) {
      this.toastr.error("Upload Image")
      return 0;
    }
    if (this.canvas.getActiveObject()) {
      return 0
    };
    if (this.canvas.getActiveObject() == null) {
      this.flagForNew = true
    }
    this.canvas.selection = false;
    this.isDown = true;
    var pointer = this.canvas.getPointer(o.e);
    this.origX = pointer.x;
    this.origY = pointer.y;
    var pointer = this.canvas.getPointer(o.e);
    this.rect = new fabric.Rect({
      left: this.origX,
      top: this.origY,
      originX: 'left',
      originY: 'top',
      width: pointer.x - this.origX,
      height: pointer.y - this.origY,
      fill: '',
      stroke: 'blue',
      type: 'rect',
      // uuid: generateUUID(),
      strokeWidth: 2,
    });
    this.canvas.add(this.rect);

  }

  shape(o) {
    if (this.flagForNew == true && this.canvas.getActiveObject() == null) {
      this.flagForMove = true
    }
    if (this.canvas.getActiveObject() != null) {
      let tmp = this.canvas.getActiveObject().aCoords;
      let srcX1 = tmp.tl.x * 2
      let srcX2 = tmp.tl.y * 2
      let srcY1 = tmp.br.x * 2
      let srcY2 = tmp.br.y * 2
      let index = this.canvas.getActiveObject().id;
      let elem = this.array
      elem[index].srcX1 = srcX1;
      elem[index].srcX2 = srcX2;
      elem[index].srcY1 = srcY1;
      elem[index].srcY2 = srcY2;

    }
    if (!this.isDown) return;
    var pointer = this.canvas.getPointer(o.e);
    if (this.origX > pointer.x) {
      this.rect.set({ left: Math.abs(pointer.x) });
    }
    if (this.origY > pointer.y) {
      this.rect.set({ top: Math.abs(pointer.y) });
    }
    this.rect.set({ width: Math.abs(this.origX - pointer.x) });
    this.rect.set({ height: Math.abs(this.origY - pointer.y) });

    this.canvas.add(this.rect).renderAll();

  }
  finish(o) {
    this.isDown = false;
    if (this.flagForNew == true && this.flagForMove == true) {
      this.rect.id = this.array.length;
      let tmp = this.rect.aCoords;
      this.rect.id = this.array.length
      let srcX1 = tmp.tl.x * 2
      let srcX2 = tmp.tl.y * 2
      let srcY1 = tmp.br.x * 2
      let srcY2 = tmp.br.y * 2
      let obj = {
        title: 'element' + this.array.length,
        srcX1: srcX1,
        srcX2: srcX2,
        srcY1: srcY1,
        srcY2: srcY2,
      }
      let arr: any = this.array
      this.array.push(obj)
      this.arrayShoworHide.push({ show: false })
      // this.array['elements']push(obj)
      this.arrayOfCanvas.push(this.rect)
      console.log(this.array)
      console.log(this.arrayShoworHide)

      this.canvasActive = true
    }
    this.flagForNew = false;
    this.flagForMove = false
  }
  add() {
    this.submitted = true;
    var name = this.form.controls.name.value
    this.canvasActive = typeof this.array !== 'undefined' && this.array.length > 0
    if ((typeof this.array !== 'undefined' && this.array.length > 0)) {
      if (name != '') {
        let obj = {
          name: name,
          elements: this.array,
        }
        this.getData.uploadFile(this.file, obj)
        // this.router.navigate(['home'])
      }
    }
  }
  delete() {
    // if (this.isFile) return 0
    this.canvas.clear()
    let empty = new Array
    this.array = []
    this.arrayShoworHide = []
    this.arrayOfCanvas = []
    this.flagNumber = undefined
    // console.log(this.array)
  }


  change(event, index = null) {
    this.arrayOfCanvas[index].set({
      stroke: 'red'
    })

    this.canvas.renderAll()
    this.arrayShoworHide[index]['show'] = true

  }
  hideAll(event) {
    console.log(this.flagNumber)
    if ($(event.target).closest('.title')[0] == undefined) {
      if(this.flagNumber!= undefined){
        this.arrayOfCanvas[this.flagNumber].set({
          stroke: 'blue'
        })
        this.flagNumber = undefined
      }
     
    } else {
      this.flagNumber = $(event.target).closest('.title')[0].id
      this.arrayOfCanvas.forEach((e, index) => {
        if (index != this.flagNumber) {
          e.set({
            stroke: 'blue'
          })
        }
      })
    }
    this.canvas.renderAll()


  }
  changeTitle(index, value) {
    // this.array.elements[index].title = value
    this.array[index]['title'] = value
    console.log(this.array[index]['title'])

  }
}
