import { Component, OnInit } from '@angular/core';
import 'fabric'
import { ServiceService } from '../service/service.service';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { log } from 'util';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { Toast, ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
declare const fabric: any;
declare const $: any
@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})
export class EditComponent implements OnInit {
  canvas: any;
  isDown: boolean; isFile: boolean = true;
  rect: any
  origX: any;
  origY: any
  listTemplate = new Array //load from service
  array: any // like listemplate
  file: File = null;
  form: FormGroup
  idTemplate: any
  flagForNew: boolean = false;
  flagForMove: boolean = false;
  flagForDelete: boolean = true;
  edit: false
  arrayShoworHide = new Array;
  keepState = false
  flagNumber: any = 0
  arrayOfCanvas = new Array

  constructor(
    private getData: ServiceService,
    private formBuilder: FormBuilder,
    private activeRoute: ActivatedRoute,
    private toastr: ToastrService,
    private router: Router,
    public spinner: NgxSpinnerService
  ) {

    this.form = formBuilder.group({
      type: ['1'],
      name: ['', Validators.required]
    })
    activeRoute.params.subscribe(val => {
      this.idTemplate = val.id;
      this.loadDataFromService()
    })
  }
  ngOnInit() {
    this.canvas = new fabric.Canvas('canvas', { selection: false });
  }
  get f() {
    return this.form.controls
  }
  // load data from cloud serivice
  loadDataFromService() {
    if (this.canvas != undefined) { this.canvas.clear() }
    let data = this.getData.getData();
    data.snapshotChanges().subscribe(item => {
      this.spinner.show()
      item.forEach(element => {
        var y = element.payload.val();
        y["$key"] = element.key
        this.listTemplate.push(y)
      });
      this.array = this.listTemplate[this.idTemplate]
      for (let i = 0; i < this.array.elements.length; i++) {

        this.arrayShoworHide.push({ show: false })
      }
      this.form.patchValue({
        name: this.array['name']
      })
      this.file = this.listTemplate[this.idTemplate]['file']
      this.isFile = false;
      fabric.Image.fromURL(this.file, (img) => {
        this.spinner.hide()
        let oImg = img.set({
          left: 0,
          top: 0,
          angle: 0,
          evented: false
        });
        img.scaleToHeight(450);
        img.scaleToWidth(600);
        this.canvas.add(oImg);
        // this.canvas.add(img).renderAll()
        this.createTemplate()

      });


    })
  }
  // crate template by data was get before
  createTemplate() {

    let i = 0;
    this.listTemplate[this.idTemplate].elements.forEach(e => {
      let x1 = e.srcX1 / 2
      let x2 = e.srcX2 / 2
      let y1 = e.srcY1 / 2
      let y2 = e.srcY2 / 2
      let rect = new fabric.Rect({
        left: x1,
        top: x2,
        originX: 'left',
        orginY: 'top',
        width: y1 - x1,
        height: y2 - x2,
        fill: '',
        stroke: 'blue',
        type: 'rect',
        strokeWidth: 2,
      })
      rect.id = i;
      this.arrayOfCanvas.push(rect)
      this.canvas.add(rect)
      i++
    });
  }

  //show img with input
  show(e) {
    this.canvas.clear()
    this.array = this.listTemplate[this.idTemplate]
    this.isFile = false
    this.file = e.target.files[0];
    const reader = new FileReader();
    reader.onload = (imgFile) => {
      const data = imgFile.target["result"];
      console.log(data)
      fabric.Image.fromURL(data, (img) => {
        let oImg = img.set({
          left: 0,
          top: 0,
          angle: 0,
          evented: false
        });
        img.scaleToHeight(450);
        img.scaleToWidth(600);
        this.canvas.add(oImg)
      });
    };
    reader.readAsDataURL(this.file);
    return false;
  }



  draw(o) {
    if (this.isFile) {
      this.toastr.error("Upload Image")
      return 0
    };
    if (this.form.controls.type.value == 2 && this.canvas.getActiveObject() == null) {
      this.flagForNew = true;

    }
    if (this.form.controls.type.value == 1) return 0;
    if (this.canvas.getActiveObject()) {
      return 0
    };
    this.canvas.selection = false;
    this.isDown = true;
    var pointer = this.canvas.getPointer(o.e);
    this.origX = pointer.x;
    this.origY = pointer.y;
    this.rect = new fabric.Rect({
      left: this.origX,
      top: this.origY,
      originX: 'left',
      originY: 'top',
      width: pointer.x - this.origX,
      height: pointer.y - this.origY,
      fill: '',
      stroke: 'blue',
      type: 'rect',
      strokeWidth: 2,
    });
    this.canvas.add(this.rect);
  }
  shape(o) {
    let i = 0
    if (this.flagForNew == true && this.form.controls.type.value == 2 && this.canvas.getActiveObject() == null) {
      console.log(this.flagForMove)
      this.flagForMove = true
    }
    if (this.canvas.getActiveObject() != null) {
      let tmp = this.canvas.getActiveObject().aCoords;
      let srcX1 = tmp.tl.x * 2
      let srcX2 = tmp.tl.y * 2
      let srcY1 = tmp.br.x * 2
      let srcY2 = tmp.br.y * 2
      let index = this.canvas.getActiveObject().id;
      let elem = this.array['elements'];
      elem[index].srcX1 = srcX1;
      elem[index].srcX2 = srcX2;
      elem[index].srcY1 = srcY1;
      elem[index].srcY2 = srcY2;

    }
    if (!this.isDown) return;
    var pointer = this.canvas.getPointer(o.e);
    if (this.origX > pointer.x) {
      this.rect.set({ left: Math.abs(pointer.x) });
    }
    if (this.origY > pointer.y) {
      this.rect.set({ top: Math.abs(pointer.y) });
    }
    this.rect.set({ width: Math.abs(this.origX - pointer.x) });
    this.rect.set({ height: Math.abs(this.origY - pointer.y) });
    this.canvas.add(this.rect)
  }
  finish(o) {
    this.isDown = false
    if (this.flagForNew == true && this.flagForMove == true) {
      this.rect.id = this.array['elements'].length;
      let tmp = this.rect.aCoords;
      let srcX1 = tmp.tl.x * 2
      let srcX2 = tmp.tl.y * 2
      let srcY1 = tmp.br.x * 2
      let srcY2 = tmp.br.y * 2
      let obj = {
        title: 'element',
        srcX1: srcX1,
        srcX2: srcX2,
        srcY1: srcY1,
        srcY2: srcY2,
      }
      this.array['elements'].push(obj);
      this.arrayOfCanvas.push(this.rect)
      this.arrayShoworHide = []
      for (let i = 0; i < this.array.elements.length; i++) {
        this.arrayShoworHide.push({ show: false })
      }
      console.log(this.arrayShoworHide)
    }
    this.flagForNew = false;
    this.flagForMove = false

  }
  delete() {
    // if (this.isFile) return 0
    this.canvas.clear()
    this.flagForDelete = false
    let empty: any = []
    this.array['elements'] = []
    this.arrayOfCanvas =[]
    this.flagNumber = undefined
    this.isFile = true;
  }
  updateTemplate() {
    let obj = {
      $key: this.array["$key"],
      name: this.f.name.value,
      elements: this.array['elements'],
    }
    this.getData.updateData(this.file, obj)
  }
  showTitle(index) {
    if (!this.keepState) {
      this.arrayShoworHide[index]['show'] = true
    }
    else { return 0 }

  }

  change(event, index = null) {
    this.arrayOfCanvas[index].set({
      stroke: 'red'
    })

    this.canvas.renderAll()
    this.arrayShoworHide[index]['show'] = true

  }
  hideAll(event) {
    console.log(this.arrayOfCanvas)
    console.log(this.arrayOfCanvas[this.flagNumber])
    if ($(event.target).closest('.title')[0] == undefined) {
      this.arrayOfCanvas.forEach((e, index) => {
          e.set({
            stroke: 'blue'
          })
      })
    } else {
      this.flagNumber = $(event.target).closest('.title')[0].id
      this.arrayOfCanvas.forEach((e, index) => {
        if (index != this.flagNumber) {
          e.set({
            stroke: 'blue'
          })
        }
      })
    }
    this.canvas.renderAll()


  }
  changeTitle(index, value) {
    // this.array.elements[index].title = value
    this.array.elements[index]['title'] = value
    console.log(this.array.elements[index]['title'])

  }


}
