import { Component, OnInit, Input } from '@angular/core';
import { ServiceService } from '../service/service.service'
import { ToastrService } from 'ngx-toastr'

@Component({
  selector: 'app-edit-table',
  templateUrl: './edit-table.component.html',
  styleUrls: ['./edit-table.component.css']
})
export class EditTableComponent implements OnInit {
  listTemplate = [];
  constructor(
    private getData: ServiceService,
    private toastr: ToastrService,
    // private activeRoute : ActivatedRoute,
    // private route : Route
  ) {
    // console.log(this.activeRoute)
  }
  @Input('time') time: any

  ngOnInit() {
    this.loadData()

  }
  ngOnChanges() {
    this.loadData()
    //Called before any other lifecycle hook. Use it to inject dependencies, but avoid any serious work here.
    //Add '${implements OnChanges}' to the class.

  }
  loadData() {
    this.listTemplate = []
    let data = this.getData.getData();
    data.snapshotChanges().subscribe(item => {
      // item.map(element => {
      //   var element = element.payload.toJSON();
      //   console.log(element.key)
      //   this.listTemplate.push(element)
      // });
      item.forEach(e => {
        var y = e.payload.toJSON();
        y["$key"] = e.key
        this.listTemplate.push(y)
      })
    })
    console.log(this.listTemplate);
  }

  delete($key) {
    console.log($key)
    if (confirm("Are you sure delete") == true) {
      this.getData.deleteTemplate($key)
      this.toastr.warning("Delete Sucessfully")
      this.listTemplate = []
      this.hideModal()
      // console.log($('.modal'))
      // this.loadData()

    }

  }
  hideModal() {
    // $('#homeModal').modal('hide');
  }
}
